import React from 'react';
import { Carousel } from 'react-responsive-carousel';
import 'react-responsive-carousel/lib/styles/carousel.min.css'; // 라이브러리의 기본 스타일을 불러옵니다.
import '../styles/notice.css'; // 추가적인 스타일을 적용합니다.

function Notice() {
    const notices = [
        {
            title: "새로운 유기견 보호소 개소",
            description: "새롭게 문을 연 유기견 보호소에서 더 많은 강아지들이 보호받을 수 있습니다.",
            image: "https://github.com/SSuaSea/react-adorable/blob/main/public/image/dog5.jpg?raw=true" // 첫 번째 슬라이드 이미지
        },
        {
            title: "입양 절차 간소화",
            description: "입양 절차가 간소화되어 더 빠르고 쉽게 반려견을 가족으로 맞이할 수 있습니다.",
            image: "https://github.com/SSuaSea/react-adorable/blob/main/public/image/dog6.jpg?raw=true" // 두 번째 슬라이드 이미지
        },
        {
            title: "봄맞이 유기견 입양 캠페인",
            description: "봄을 맞아 유기견 입양 캠페인을 진행합니다. 많은 관심 부탁드립니다.",
            image: "https://github.com/SSuaSea/react-adorable/blob/main/public/image/dog7.jpg?raw=true" // 세 번째 슬라이드 이미지
        }
    ];

    return (
        <div className="notice">
            <Carousel showArrows={false} autoPlay={true} infiniteLoop={true} interval={3000} showThumbs={false}>
                {notices.map((notice, index) => (
                    <div key={index} className="notice-slide" style={{ backgroundImage: `url(${notice.image})` }}>
                        <div className="notice-content">
                            <span className="notice-label">안내사항</span>
                            <h2>{notice.title}</h2>
                            <p>{notice.description}</p>
                        </div>
                    </div>
                ))}
            </Carousel>
        </div>
    );
}

export default Notice;

