import React from 'react';
import { useParams } from 'react-router-dom';
import storeData from '../data/storeData.js';
import '../styles/productdetail.css';

function ProductDetail() {
    const { id } = useParams();
    const product = storeData.find(item => item.id === Number(id));


    // 디버깅용 콘솔 로그
    console.log('Product ID:', id);
    console.log('Found Product:', product);

    if (!product) {
        return <div>상품을 찾을 수 없습니다.</div>;
    }

    return (
        <div className="product-detail">
            <h2>{product.name || '상품명 없음'}</h2>
            <img src={product.image || '이미지 없음'} alt={product?.name || '이미지 없음'} />
            <p>가격: {product.price ? `${product.price}원` : '가격 없음'}</p>
            <p>카테고리: {product.category || '카테고리 없음'}</p>
        </div>
    );
    
}


export default ProductDetail;
